import React from 'react';
import AbstractList from '../Core/AbstractList'

const ButtonStyle = {
    marginLeft: "10px"
}



class GoodsRawMaterial extends AbstractList {

	constructor( props ) {
		super( props );
		this.getCreatePanel = this.getCreatePanel.bind( this );
	}

	getEntityName() {
		return "goodsRawMaterial";
	}

	getSearchPanel() {
		return [
			{ id: "EQ:title", type: 'text', label: '' },
			{ id: "EQ:category.id", type: 'combo', label: '  ',
				values: this.state.options ? this.state.options.categories : [],
				convertToVal: it => it.id, convertToStr: it => it.title },
			{ id: "EQ:material.id", type: 'combo', label: ' ',
				values: this.state.options ? this.state.options.materials : [],
				convertToVal: it => it.id, convertToStr: it => it.title },
			{ id: "EQ:goods.id", type: 'combo', label: '',
				values: this.state.options ? this.state.options.products : [],
				convertToVal: it => it.id, convertToStr: it => it.title }
		]
	}

	getTableHead() {
		return ["", "" , "", " ", "", ""];
	}

	getCreatePanel() {
		return [
			{ id: "title", type: 'text', label: '' },
			{ id: "importFactor", type: 'text', label: ' ' },
			this.createCombo( "category", '', "categories" ),
			this.createCombo( "material", ' ', "materials" ),
			this.createCombo( "goods", '', "products" ),
			
		];
	}

	saveEntity(entity) {
		this.refineComboBox("category", entity);
		this.refineComboBox("goods", entity);
		this.refineComboBox("material", entity);
		super.saveEntity( entity );
	}

	renderTitle( entity ) {
		return ( entity ? entity.title : '');
	}

	convertToTableRow( data ) {
		return (
			<tr key={"item_" + data.id}>
				<td>{data.id}</td>
				<td>{data.title}</td>
				<td>{data.selectAble ? this.renderTitle( data.category ) : this.renderTitle( data.material ) }</td>
				<td>{data.importFactor}</td>
				<td>{data.goods.title}</td>
				<td>
				<a style={ButtonStyle} href="javascript:void(0);"><i className="fa fa-pencil" aria-hidden="true" onClick={this.editEntity.bind(this, data.id)}></i></a>
				</td>
			</tr>
		);
	}

}


export default GoodsRawMaterial;


